import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatSort } from '@angular/material/sort';
import { MainServicesService } from './../../../services/main-services.service';
import { Router } from '@angular/router';
import $ from 'jquery';
import { AlertController, ModalController } from "@ionic/angular";
import { AddBranchModalComponent } from '.././add-branch-modal/add-branch-modal.component';
import { EditBranchModalComponent } from ".././edit-branch-modal/edit-branch-modal.component";
import { Sort } from '@angular/material/sort';
import { ToastrService } from 'ngx-toastr';
import { LoaderService } from 'src/app/services/loader.service';


@Component({
  selector: 'app-branches-list',
  templateUrl: './branches-list.page.html',
  styleUrls: ['./branches-list.page.scss'],
})

export class BranchesListPage implements OnInit {
  showText: boolean;

  displayedColumns: string[] = ['position', 'name', 'weight', 'symbol'];
  branchList = [];
  sortedData = [];
  // showMessage:boolean = false;
  sucmsg;
  errMsg;

  // hrithik
  pageCounts = Array();  // Will Push total no. of pages in this array
  pageCount: number;    // Total no. of Pages for Branch List (data from API)
  page: number;         // Current Page (data from API)

  leftSize: number;
  rightSize: number;
  showMenu: boolean = false;

  @ViewChild(MatSort) sort: MatSort;


  constructor(private branchData: MainServicesService, private router: Router, private modalController: ModalController,
    private alertCtrl: AlertController, private toastr: ToastrService, private loader:LoaderService) { }

  addModal() {
    this.showText = true;
  }

  editModal() {
    this.showText = false;
  }

  ngOnInit() {
  }

  manuToggle() {
    this.showMenu = !this.showMenu;

    if (this.showMenu == false) {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 3;
        this.rightSize = 9;
      }
      else {
        this.leftSize = 2;
        this.rightSize = 10;
      }

      $('#firstdiv').removeClass("small-aside");
      $('#seconddiv').removeClass("big-right-side");
    }
    else {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      else {
        this.leftSize = 1;
        this.rightSize = 11;
      }

      $('#firstdiv').addClass("small-aside");
      $('#seconddiv').addClass("big-right-side");
    }
  }

  hide_mobile_menu() {
    $('#close-show-991').click(function () {
      $('#firstdiv').removeClass("small-aside");
    })
  }

  showMenuMob = true;
  mobileManuToggle() {
    $('#firstdiv').addClass("small-aside");
  }


  ionViewWillEnter() {
    if ($(window).width() < 992) {
      this.rightSize = 12;
    }
    else if ($(window).width() < 1367) {
      this.leftSize = 3;
      this.rightSize = 9;
    }
    else {
      this.leftSize = 2;
      this.rightSize = 10;
    }

    this.pageCounts = [];
    this.branchList = [];
    this.branchData.getBranchesList().subscribe(
      (result) => {
        this.pageCount = result['pageCount'];
        this.page = result['page'];
        for (let i = 1; i <= result['pageCount']; i++) {
          this.pageCounts.push(i);
        }
        this.branchList = result['branches'];
        this.sortedData = this.branchList.slice();
      })

    $(document).ready(function () {
      $('.mat-sort-header-content').css({
        // 'margin':'auto'
      })
      $('.mat-sort-header-indicator, .mat-sort-header-stem').css({
        'color': '#fff'
      })
      $('.mat-sort-header-content').css({
        "width": "100%",
        // "padding-left": "41px"
      })
    })
  }

  async openModal() {
    const modal = await this.modalController.create({
      component: AddBranchModalComponent,
      cssClass: 'my-custom-class'
    });

    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();

    this.sucmsg = result['message'];
    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }
  }

  async openeditModal(list) {
    const modal = await this.modalController.create({
      component: EditBranchModalComponent,
      componentProps: {
        _id: list._id,
      },
      cssClass: 'my-edit-branch-class'
    });

    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();
    this.sucmsg = result['message'];
  
    if (result['success'] == true) {
      // this.showMessage = true;
      // $('#successMsg').fadeIn(1000);
      // $('#successMsg').fadeOut(5000);
      // this.showMessage = false;
      // this.ionViewWillEnter();
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    
    }
  }

  // Function to switch b/w pages
  getNextData(pageNo) {
    this.branchList = [];
    this.branchData.getPagination(pageNo).subscribe(
      (data) => {
        this.page = data['page'];
        this.branchList = data['branches'];
        this.sortedData = this.branchList.slice();
      },
      (error) => {
        console.log(error);
      }
    )
  }

  sortData(sort: Sort) {
    const data = this.branchList.slice();

    if (!sort.active || sort.direction === '') {
      this.sortedData = data;
      return;
    }

    this.sortedData = data.sort((a, b) => {
      const isAsc = sort.direction === 'asc';
      switch (sort.active) {
        case 'name': return compare(a.name, b.name, isAsc);
        case 'description': return compare(a.description, b.description, isAsc);
        default: return 0;
      }
    });
  }

  delete_branch(id) {
    this.branchData.deleteBranch(id).subscribe(
      (data) => {
        this.sucmsg = data['message'];
        this.ionViewWillEnter();
        this.toastr.success(this.sucmsg);
      }, (error) => {
        console.log(error);
      }
    )
  }

  async presentAlert(id) {
    const alert = await this.alertCtrl.create({
      cssClass: 'my-alert-class',
      subHeader: 'Do you really want to delete it ?',
      // message: 'This is an alert message.',
      buttons: [{
        text: 'Cancel'
      }, {
        text: 'yes',
        handler: () => {
          this.delete_branch(id)
        }
      }]
    });

    await alert.present();
  }
  

  branchLists = [];
  branch_search(searchData) {
    this.branchLists = [];
    this.pageCounts = []
    this.branchData.searchBranchApi(searchData).subscribe(

      (data) => {
        this.branchList = data['branches'];
        this.sortedData = this.branchList.slice();

        // data['branches'].forEach((value) => {
        //   this.branchLists.push({
        //     _id: value._id,
        //     branch_name: value.name,
        //     description: value.description
        //   });
        // });

        // this.branchLists

      }


    )


  }

  











}

function compare(a: number | string, b: number | string, isAsc: boolean) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
