import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { HeaderComponent } from './../../../common/header/header.component';
import $ from 'jquery';
import { MainServicesService } from './../../../services/main-services.service';
import { ModalController } from "@ionic/angular";



@Component({
  selector: 'app-add-branch-modal',
  templateUrl: './add-branch-modal.component.html',
  styleUrls: ['./add-branch-modal.component.scss'],
})
export class AddBranchModalComponent implements OnInit {

 
   
  selectedCar: number;

  cars = [
      { id: 1, name: 'Volvo' },
      { id: 2, name: 'Saab' },
      { id: 3, name: 'Opel' },
      { id: 4, name: 'Audi' },
  ];

  branchForm:FormGroup;

 
  // balanceInput = new FormControl('', Validators.required);

  constructor(private formbuilder: FormBuilder, private router: Router,
     private branchData: MainServicesService, private modalController: ModalController ) {
    this.branchForm = this.formbuilder.group({
      // name: ['',Validators.required],
      // description:['',Validators.required]
      name: new FormControl(''),
      description: new FormControl('')
    })
   }

   branchName:string;
   branchDesc:any;
   modalCtrl;
   errMsg;



 
   ngOnInit() {

    $('ng-value-label ng-star-inserted').text('hello');
   
  }

  branch_name(e){
    this.branchName = e.target.value
  }

  branch_description(e){
    this.branchDesc = e.target.value
  }
  submit()
  {
   let addBranch = {
     name : this.branchName,
     description : this.branchDesc
   }

    this.branchData.addBranch(addBranch).subscribe((result)=>{
      this.modalController.dismiss(result, 'success');
    },
    (error)=>{
      this.errMsg = error['error']['error']['name'];
      if(this.errMsg){
        $('#errors').fadeIn(1000);
      }
      // this.modalController.dismiss(error, 'success');
 
    })
    // this.router.navigate(['/branches-list']);
    // this.router.navigateByUrl('/branches-list');
    // window.location.reload();
    
  }

  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }
  // onDeposit(value){
  //   let branchData = {
  //     name: this.balanceInput.value
  //   }
  //   this.branchData.addBranch(branchData).subscribe((result)=>{
  //     console.log(result);      
  //   })
  //   this.modalController.dismiss(branchData, 'success');
    
  // }

  


}
